/*    
nav items toogle
*/
.nav-toggle-label {
  cursor: pointer;
  z-index: 999;
  max-width: 20vw;
}
.nav-toggle-label span,
.nav-toggle-label span::after,
.nav-toggle-label span::before {
  display: block;
  background: #fffffa;
  height: 3px;
  width: 32px;
  border-radius: 5px;
  transition: transform 0.5s, bottom 0.5s, top 0.5s, background 0.5s;
}
.nav-toggle-label span::after,
.nav-toggle-label span::before {
  content: "";
  position: relative;
}
.nav-toggle-label span::before {
  bottom: 15px;
}
.nav-toggle-label span::after {
  top: 10px;
}

.nav-toggle {
  align-self: center;
  margin: 0px auto;
}
.nav-toggle:checked ~ .nav-toggle-label span {
  background: transparent;
}
.nav-toggle:checked ~ .nav-toggle-label span::before {
  bottom: 0px;
  transform: rotate(405deg);
}
.nav-toggle:checked ~ .nav-toggle-label span::after {
  top: -3px;
  transform: rotate(-405deg);
}
.nav-toggle:checked ~ .nav-items .nav-item_container {
  max-height: 100px;
  padding: 1rem 2rem;
}

@media only screen and (min-width: 1200px) {
  .nav-toggle-label {
    display: none;
  }

  .nav-items {
    position: relative;
    top: 0;
  }
  .nav-items .nav-item_container {
    max-height: unset;
    background: transparent;
  }
  .nav-items .nav-item_book {
    top: 0;
    right: 0;
    position: relative;
    transform: none;
  }
  .nav-items .nav-link {
    overflow: visible;
  }
  .nav-items li {
    position: relative;
    text-align: center;
  }
  .nav-items a {
    justify-content: center;
    text-transform: uppercase;
  }
  .nav-items svg, .nav-items img {
    height: 50px;
  }

  .nav-toggle:checked ~ .nav-items .nav-item_container {
    height: 100%;
    padding: 0rem;
  }
}
.nav-toggle {
  display: none;
}

